import './EventList.scss';

import {
    ConstrainMode,
    DetailsList,
    DetailsListLayoutMode,
    IBasePickerSuggestionsProps,
    IDetailsColumnRenderTooltipProps,
    IDetailsHeaderProps,
    IInputProps,
    IRenderFunction,
    IStackTokens,
    ITag,
    ScrollablePane,
    ScrollbarVisibility,
    SelectionMode,
    Stack,
    Sticky,
    StickyPositionType,
    TagPicker,
    TooltipHost
} from '@fluentui/react';
import React from 'react';
// import moment from 'moment';

import { UserEvent } from './UserEvent';
import moment from 'moment';
import { TimeUtil } from './TimeUtil';

interface Props {
    events: UserEvent[];
}

interface State {
    filterEvent: (e: UserEvent) => boolean;
}

export class EventList extends React.Component<Props, State>{
    state: Readonly<State> = {
        filterEvent: (e) => true,
    }

    _columns = [
        { key: 'column1', name: 'Title', fieldName: 'title', minWidth: 25, maxWidth: 200, isResizable: true, flexGrow:2 },
        { key: 'column2', name: 'Time', fieldName: 'startTime', minWidth: 25, maxWidth: 200, isResizable: true, flexGrow:2 },
        //{ key: 'column3', name: 'End Time', fieldName: 'endTime', minWidth: 20, maxWidth: 500, isResizable: true, flexGrow:2 },
        { key: 'column3', name: 'Duration', fieldName: 'duration', minWidth: 25, maxWidth: 200, isResizable: true, flexGrow:2 },
        { key: 'column4', name: 'Tags', fieldName: 'tags', minWidth: 20, maxWidth: 200, isResizable: true, flexGrow:8 },
    ];

    render() {
        const items = this.props.events
            .filter(e => this.state.filterEvent(e))
            .map((e, idx) => {
                let duration = moment.duration(e.getDuration());
                let st = TimeUtil.GetHourAndMinFromDate(e.startTime);
                return {
                    key: idx,
                    title: e.title,
                    startTime: st.hour + ":"+ st.min,
                    //endTime: e.endTime?.toTimeString(),
                     duration: duration.hours() + "h "+duration.minutes()+"m",
                    tags: [...e.tags].join("; "),
                };
            });

        const stackTokens: IStackTokens = {
            childrenGap: 80, // this adds margin-top: 80px; otherwise ScrollablePane shows on top of TextField
        };

        const inputProps: IInputProps = {
            onBlur: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onBlur called'),
            onFocus: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onFocus called'),
        };

        const pickerSuggestionsProps: IBasePickerSuggestionsProps = {
            suggestionsHeaderText: 'Suggested tags',
            noResultsFoundText: 'No color tags found',
        };

        // should aggregate tags in data manager layer and pass as component props
        const suggestedTags: ITag[] = [...this.props.events
            .reduce((prev, curr) => {
                curr.tags.forEach(t => prev.add(t));
                return prev;
            }, new Set<string>())]
            .map(item => ({ key: item, name: item }));

        const listContainsTagList = (tag: ITag, tagList?: ITag[]) => {
            if (!tagList || !tagList.length || tagList.length === 0) {
                return false;
            }
            return tagList.some(compareTag => compareTag.key === tag.key);
        };

        const filterSuggestedTags = (filterText: string, tagList?: ITag[]): ITag[] => {
            if (!filterText) {
                return [];
            }

            let suggestion = suggestedTags.filter(
                tag => tag.name.toLowerCase().indexOf(filterText.toLowerCase()) === 0 && !listContainsTagList(tag, tagList),
            );
            if (!suggestion.length) {
                suggestion.push({ key: filterText, name: filterText });
            }
            return suggestion;
        };

        const getTextFromItem = (item: ITag) => item.name;

        return (
            <div className="events-container">
                <Stack className="events-components" verticalFill={true} tokens={stackTokens}>
                    {/* <TextField
                        className="filter"
                        label="Filter by tag:"
                        onChange={this._onFilter}
                        styles={textFieldStyles}
                    /> */}

                    <TagPicker
                        removeButtonAriaLabel="Remove"
                        selectionAriaLabel="Selected colors"
                        onResolveSuggestions={filterSuggestedTags}
                        onChange={(items) => {
                            console.log("items: " + items?.map(t => t.name));

                            this.setState({
                                filterEvent: (e) => {
                                    if (!Array.isArray(items) || items.length === 0) {
                                        return true;
                                    }

                                    return items
                                        .map(t => t.name)
                                        .every(t => e.tags.has(t));
                                    // const itemsSet = new Set(items.map(t => t.name));
                                    // const intersection = [...e.tags].filter(t => itemsSet.has(t));
                                    // return intersection.length > 0;
                                },
                            });
                        }}
                        onItemSelected={(item?) => { console.log("selected " + item?.name); return item!; }}
                        getTextFromItem={getTextFromItem}
                        pickerSuggestionsProps={pickerSuggestionsProps}
                        // itemLimit={2}
                        disabled={false}
                        inputProps={{
                            ...inputProps,
                            id: 'picker1',
                        }}
                    />

                    <ScrollablePane className="events-table" scrollbarVisibility={ScrollbarVisibility.auto}>
                        <DetailsList
                            compact={true}
                            items={items}
                            columns={this._columns}
                            setKey="set"
                            layoutMode={DetailsListLayoutMode.fixedColumns}
                            selectionMode={SelectionMode.none}
                            constrainMode={ConstrainMode.unconstrained}
                            onRenderDetailsHeader={onRenderDetailsHeader}

                            onItemInvoked={() => console.log("clicked")}
                            ariaLabelForSelectionColumn="Toggle selection"
                            ariaLabelForSelectAllCheckbox="Toggle selection for all items"
                            checkButtonAriaLabel="select row"
                        />
                    </ScrollablePane>
                </Stack>
            </div >
        );
    }

    // private _onFilter = (ev: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>, text?: string): void => {
    //     this.setState({
    //         filterEvent: (e) => {
    //             if (text) {
    //                 for (const tag of e.tags) {
    //                     if (tag.startsWith(text)) {
    //                         return true;
    //                     }
    //                 }
    //                 return false;
    //             }
    //             return true;
    //         },
    //     });
    // };
}

const onRenderDetailsHeader: IRenderFunction<IDetailsHeaderProps> = (detailsHeaderProps, defaultRender) => {
    if (!detailsHeaderProps) {
        return null;
    }

    const onRenderColumnHeaderTooltip: IRenderFunction<IDetailsColumnRenderTooltipProps> = tooltipHostProps => (
        <TooltipHost {...tooltipHostProps} />
    );
    return (
        <Sticky stickyPosition={StickyPositionType.Both}>
            {defaultRender!({
                ...detailsHeaderProps,
                onRenderColumnHeaderTooltip,
            })}
        </Sticky>
    );
};
