import { UserEvent, IUserEventObject } from "./UserEvent"

export class UserEventStore {
    saveUserEvents(userEvents: UserEvent[]) {
        let transformedUserEvents = userEvents.map(e => {
            return {
                title: e.title,
                startTime: e.startTime,
                endTime: e.endTime,
                tags: [...e.tags], // JSON.stringify() cannot serialize Set
            };
        });

        let serializedUserEvents = JSON.stringify(transformedUserEvents);
        localStorage.setItem("data", serializedUserEvents);
    }

    loadUserEvents(): UserEvent[] {
        let serializedUserEvents = localStorage.getItem("data");
        if (serializedUserEvents) {
            let data = JSON.parse(serializedUserEvents) as IUserEventObject[];
            let result = data.map((element) => {
                let v = new UserEvent(new Date(element.startTime), element.title);
                if (element.endTime !== undefined)
                {
                    v.endTime = new Date(element.endTime); // JSON.parse() cannot parse Date
                }
                v.tags = new Set(element.tags);
                return v;
            });
            return result;
        }
        return [];
    }
}