import { Duration } from "moment";

export class TimeUtil {
    static GetMonthDayHash(date: Date): number {
        let m = date.getMonth() + 1;
        let d = date.getDate();
        return m * 32 + d;
    }

    static GetMonthDayStringFromHash(dateHash: number): string {
        let m = Math.floor(dateHash / 32);
        let d = dateHash % 32;
        return m + "/" + d;
    }

    static GetHourAndMinFromDuration(duration: Duration) {
        let hour = duration.hours();
        let min = duration.minutes();
        return { hour: (hour >= 10 ? "" : "0") + hour, min: (min >= 10 ? "" : "0") + min };
    }

    static GetHourAndMinFromDate(date: Date) {
        let hour = date.getHours();
        let min = date.getMinutes();
        return { hour: (hour >= 10 ? "" : "0") + hour, min: (min >= 10 ? "" : "0") + min };
    }
}

