import {
    IBasePickerSuggestionsProps,
    IInputProps,
    ITag,
    TagPicker
} from "@fluentui/react";
import React from "react";
import { UserEvent } from "../UserEvent";
import * as UserEventManager from "../UserEventManager"
import { UserEventStore } from "../UserEventStore";

class Debug extends React.Component {
    render() {
        return (
            <div className="DebugPage" >
                <h1>Lyubishchev</h1>
                <p>
                    <button
                        onClick={() => {
                            let eventName = [
                                "Test Event",
                                Math.random().toString(16).substr(2, 4),
                            ].join(" ");
                            let newEvent = new UserEvent(new Date());
                            UserEventManager.instance.addEvent(newEvent);

                            console.log("added event: " + eventName);
                        }}>
                        Start an event
                    </button>
                </p>

                <p>
                    <button
                        onClick={() => {
                            let userEvents = UserEventManager.instance.getEvents();
                            let latestUserEvent = userEvents[userEvents.length - 1];

                            latestUserEvent.endTime = new Date();

                            let dataStore = new UserEventStore();
                            dataStore.saveUserEvents(userEvents);

                            console.log("updated event: " + latestUserEvent.title);
                        }}>
                        End a started event
                    </button>
                </p>

                <p>
                    <button onClick={() => {
                        let userEvents = UserEventManager.instance.getEvents();
                        console.log(userEvents);
                    }}>
                        print events
                    </button>
                </p>

                <p>
                    <button onClick={() => {
                        let userEvents = UserEventManager.instance.getEvents();
                        console.log(userEvents.map((element) => {
                            return {
                                ...element,
                                duration: element.getDuration()
                            };
                        }));
                    }}>
                        print events with derived details
                    </button>
                </p>

                <p>
                    <button onClick={() => {
                        UserEventManager.instance.clearEvents();
                        console.log("cleared all events");
                    }}>
                        clear events
                    </button>
                </p>

                <p>
                    <button onClick={() => {
                        UserEventManager.instance.generateRandomEvents();
                        console.log("random events generated");
                    }}>
                        generate random events
                    </button>
                </p>

                <TagPicker
                    removeButtonAriaLabel="Remove"
                    selectionAriaLabel="Selected colors"
                    onResolveSuggestions={filterSuggestedTags}
                    onChange={(items) => console.log("items: " + items?.map(t => t.name))}
                    onItemSelected={(item?) => { console.log("selected " + item?.name); return item!; }}
                    getTextFromItem={getTextFromItem}
                    pickerSuggestionsProps={pickerSuggestionsProps}
                    itemLimit={2}
                    disabled={false}
                    inputProps={{
                        ...inputProps,
                        id: 'picker1',
                    }}
                />
            </div>
        );
    }
}

// begin TagPicker test

const inputProps: IInputProps = {
    onBlur: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onBlur called'),
    onFocus: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onFocus called'),
};

const pickerSuggestionsProps: IBasePickerSuggestionsProps = {
    suggestionsHeaderText: 'Suggested tags',
    noResultsFoundText: 'No color tags found',
};

const testTags: ITag[] = [
    'black',
    'blue',
    'brown',
    'cyan',
    'green',
    'magenta',
    'mauve',
    'orange',
    'pink',
    'purple',
    'red',
    'rose',
    'violet',
    'white',
    'yellow',
].map(item => ({
    key: item,
    name: item
}));

const listContainsTagList = (tag: ITag, tagList?: ITag[]) => {
    if (!tagList || !tagList.length || tagList.length === 0) {
        return false;
    }
    return tagList.some(compareTag => compareTag.key === tag.key);
};

const filterSuggestedTags = (filterText: string, tagList?: ITag[]): ITag[] => {
    if (!filterText) {
        return [];
    }

    let suggestion = testTags.filter(
        tag => tag.name.toLowerCase().indexOf(filterText.toLowerCase()) === 0 && !listContainsTagList(tag, tagList),
    );
    if (!suggestion.length) {
        suggestion.push({ key: filterText, name: filterText });
    }
    return suggestion;
};

const getTextFromItem = (item: ITag) => item.name;

// end TagPicker test

export default Debug;
