import './App.scss';

import React from 'react';
import LoggingMode from './LoggingMode';
import { Dashboard } from './Dashboard';
import { CommandBarButton, DefaultButton, FontIcon, Panel, PanelType, Stack } from '@fluentui/react';

class App extends React.Component {
    state = {
        cookie: document.cookie,
        isPanelOpen: false,
        selectedMode: "logging"
    };

    render() {
        const app = this.state.selectedMode === "logging"
            ? <LoggingMode className="App-content" />
            : <Dashboard className="App-content" />;

        return (
            <div className="App">
                <div className="panel-container">
                    <DefaultButton className="burger-button" onClick={() => this.setState({ isPanelOpen: true })}>
                        <FontIcon
                            className="icon"
                            aria-label="GlobalNavButton"
                            iconName="GlobalNavButton" />
                    </DefaultButton>
                    <Panel
                        className="nav-panel"
                        isLightDismiss
                        isOpen={this.state.isPanelOpen}
                        onDismiss={() => this.setState({ isPanelOpen: false })}
                        headerText="Mode"
                        type={PanelType.smallFixedNear}>
                        <Stack>
                            <CommandBarButton text="Logging" onClick={() => this.setState({ selectedMode: "logging" })} />
                            <CommandBarButton text="Dashboard" onClick={() => this.setState({ selectedMode: "dashboard" })} />
                        </Stack>
                    </Panel>
                </div>
                {app}
            </div>);

    }
}

export default App;
