import { Stack, IStackTokens } from '@fluentui/react';
import { PrimaryButton } from '@fluentui/react/lib/Button';
import { FontIcon } from '@fluentui/react/lib/Icon';
import './LoggingMode.scss';
import React from 'react';

import EventDialog from './EventDialog';
import * as UserEventManager from "./UserEventManager";
import { UserEvent } from './UserEvent';
import Timer from './Timer';
import { EventList } from './EventList';

class LoggingMode extends React.Component<{ className?: string }> {
    state = {
        cookie: document.cookie,
        onGoingEvent: this.initializeIsPlaying(),
        hideDialog: true
    };

    private startTime?: Date = undefined;
    private endTime?: Date = undefined;

    render() {
        const stackTokens: IStackTokens = {
            childrenGap: 80, // this adds margin-top: 80px; otherwise ScrollablePane shows on top of TextField
        };

        return (
            <div className={"logging-mode-container " + this.props.className}>
                <Timer startTime={this.state.onGoingEvent?.startTime} />
                <Stack className="timer-controls" horizontal tokens={stackTokens}>
                    {
                        !this.state.onGoingEvent ?
                            <PrimaryButton
                                className="button"
                                onClick={() => this.onStartClicked()}
                                allowDisabledFocus>
                                <FontIcon
                                    className="icon"
                                    aria-label="Play"
                                    iconName="Play" />
                            </PrimaryButton> :
                            <PrimaryButton
                                className="button"
                                onClick={() => this.onStopClicked()}
                                allowDisabledFocus>
                                <FontIcon
                                    className="icon"
                                    aria-label="Stop"
                                    iconName="Stop" />
                            </PrimaryButton>
                    }
                    {/* <PrimaryButton
                        className="button"
                        onClick={() => {}}
                        disabled={!this.state.playing}>
                        <FontIcon
                            className="icon"
                            aria-label="Next"
                            iconName="Next" />
                    </PrimaryButton> */}
                </Stack>

                <EventDialog
                    hidden={this.state.hideDialog}
                    startTime={this.startTime}
                    endTime={this.endTime}
                    hideDialog={() => this.hideSubmitDialog()}
                    restartPlay={() => this.startPlay()}
                />

                <EventList events={UserEventManager.instance.getEvents()} />
            </div>
        );
    }

    onStartClicked(): void {
        this.startPlay();
    }

    startPlay(): void {
        let currentTime = new Date();
        let newEvent = new UserEvent(currentTime);
        this.startTime = currentTime;
        this.setState({ onGoingEvent: newEvent });
        UserEventManager.instance.addEvent(newEvent);

        console.log("start event: " + currentTime);
    }

    onStopClicked(): void {
        this.endTime = new Date();
        this.showSubmitDialog();
        this.setState({ onGoingEvent: undefined });

        console.log("stop clicked: " + this.endTime);
    }

    showSubmitDialog(): void {
        this.setState({
            hideDialog: false,
        })
    }

    hideSubmitDialog(): void {
        this.setState({
            hideDialog: true,
        })
    }

    initializeIsPlaying(): UserEvent | undefined {
        const events = UserEventManager.instance.getEvents();
        if (events.length > 0 && !events[events.length - 1].endTime) {
            return events[events.length - 1];
        }

        return undefined;
    }
}

export default LoggingMode;
