import './Timer.scss';

import { Label } from '@fluentui/react';
import React from 'react';
import moment from 'moment';
import { TimeUtil } from './TimeUtil';

function delay(second: number): Promise<void> {
    return new Promise(resolve =>
        setTimeout(resolve, second * 1000));
}

interface Props {
    startTime?: Date;
}

interface State {
    timer: string;
}

class Timer extends React.Component<Props, State> {
    state: Readonly<State> = {
        timer: "00:00",
    };
    started = false;

    render() {
        if (!this.started && this.props.startTime != null) {
            this.started = true;
            this.startTimer();
        }

        return (
            <Label className="timer">{this.state.timer}</Label>
        );
    }

    async startTimer() {
        var showColon = true;
        while (this.props.startTime != null) {
            let timespan = moment.duration(Date.now() - this.props.startTime.getTime());
            let pair = TimeUtil.GetHourAndMinFromDuration(timespan);
            let timer = pair.hour + (showColon ? ":" : " ") + pair.min;
            this.setState({ timer: timer });
            showColon = !showColon;
            await delay(1);
        }
        this.started = false;
    }
}

export default Timer;
