import './EventDialog.scss';

import { Dialog, DialogFooter, Label, TagPicker, IInputProps, IBasePickerSuggestionsProps, ITag, TextField } from '@fluentui/react';
import { DefaultButton, PrimaryButton } from '@fluentui/react/lib/Button';
import React from 'react';
import moment from 'moment';

import * as UserEventManager from "./UserEventManager"
import { UserEventStore } from './UserEventStore';

class EventDialog extends React.Component<{ hidden: boolean, startTime?: Date, endTime?: Date, hideDialog: () => void, restartPlay: () => void }> {
    state = {
        title: "",
        tags: [],
        hidden: false
    };

    render() {
        const inputProps: IInputProps = {
            onBlur: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onBlur called'),
            onFocus: (ev: React.FocusEvent<HTMLInputElement>) => console.log('onFocus called'),
        };

        const pickerSuggestionsProps: IBasePickerSuggestionsProps = {
            suggestionsHeaderText: 'Suggested tags',
            noResultsFoundText: 'No color tags found',
        };

        const suggestedTags: ITag[] = []
            .map(item => ({ key: item, name: item }));

        const listContainsTagList = (tag: ITag, tagList?: ITag[]) => {
            if (!tagList || !tagList.length || tagList.length === 0) {
                return false;
            }
            return tagList.some(compareTag => compareTag.key === tag.key);
        };

        const filterSuggestedTags = (filterText: string, tagList?: ITag[]): ITag[] => {
            if (!filterText) {
                return [];
            }

            let suggestion = suggestedTags.filter(
                tag => tag.name.toLowerCase().indexOf(filterText.toLowerCase()) === 0 && !listContainsTagList(tag, tagList),
            );
            if (!suggestion.length) {
                suggestion.push({ key: filterText, name: filterText });
            }
            return suggestion;
        };

        const getTextFromItem = (item: ITag) => item.name;

        return (
            <Dialog hidden={this.props.hidden} title={"Log an event"} className="event-dialog">
                <Label className="time-label">{this.props.startTime?.toLocaleTimeString() + " - " + this.props.endTime?.toLocaleTimeString()}</Label>
                <Label className="time-label">{"Total time " + this.getTimeDiff()}</Label>
                <TextField
                    placeholder={"Title"}
                    className="title-input"
                    onChange={(e, val) => this.setState({ title: val })}>
                </TextField>
                <TagPicker
                    removeButtonAriaLabel="Remove"
                    selectionAriaLabel="Selected colors"
                    onResolveSuggestions={filterSuggestedTags}
                    onChange={(items) => {
                        console.log("items: " + items?.map(t => t.name));
                        this.setState({ tags: items?.map(t => t.name) ?? [] })
                    }}
                    onItemSelected={(item?) => { console.log("selected " + item?.name); return item!; }}
                    getTextFromItem={getTextFromItem}
                    pickerSuggestionsProps={pickerSuggestionsProps}
                    // itemLimit={2}
                    disabled={false}
                    inputProps={{
                        ...inputProps,
                        id: 'picker1',
                        placeholder: 'Tags'
                    }}
                />

                <DialogFooter >
                    <PrimaryButton
                        text="Log"
                        onClick={() => this.onSubmit()} />
                    <DefaultButton text="Cancel" onClick={() => this.onCancel()} />
                </DialogFooter>

            </Dialog>
        );
    }

    getTimeDiff(): string {
        if (!this.props.endTime || !this.props.startTime) {
            return "";
        }

        const timespan = this.props.endTime.getTime() - this.props.startTime.getTime();
        const timespan2 = moment.duration(timespan);
        const hour = timespan2.hours();
        const min = timespan2.minutes();
        return (hour > 0 ? `${hour}h` : "") + `${min}m`;
    }

    onSubmit(): void {
        let userEvents = UserEventManager.instance.getEvents();
        let latestUserEvent = userEvents[userEvents.length - 1];

        latestUserEvent.title = this.state.title;
        latestUserEvent.endTime = this.props.endTime;
        latestUserEvent.tags = new Set<string>(this.state.tags);

        let dataStore = new UserEventStore();
        dataStore.saveUserEvents(userEvents);

        console.log(userEvents);
        this.props.hideDialog();
        // this.props.restartPlay();
    }

    onCancel() {
        this.props.hideDialog();
    }
}

export default EventDialog;
