function AxisOn0({
    xDomain = [-5, 5],
    yDomain = [-5, 5],
    xAxisOn0 = true,
    yAxisOn0 = true,
    verticalTickValues = [],
    horizontalTickValues = [0]
}): ReactNode {
    const PI =  Math.PI;
    const myData = [
        {angle0: 0, angle: Math.PI / 4, opacity: 0.2, radius: 2, radius0: 1},
        {angle0: PI / 4, angle: 2 * PI / 4, radius: 100, radius0: 0},
        {angle0: 2 * PI / 4, angle: 3 * PI / 4, radius: 2, radius0: 0},
        {angle0: 3 * PI / 4, angle: 4 * PI / 4, radius: 2, radius0: 0},
        {angle0: 4 * PI / 4, angle: 5 * PI / 4, radius: 2, radius0: 0},
        {angle0: 0, angle: 5 * PI / 4, radius: 1.1, radius0: 0.8}
      ]
    return (
        
        <XYPlot
        xDomain={[-1, 1]}
        yDomain={[-1, 1]}
        width={300}
        height={300}>
        <ArcSeries
          animation
          radiusType={'literal'}
          center={{x: 0, y: 0}}
          data={myData}
          colorType={'literal'}/>
      </XYPlot>
    );
}