export interface IUserEventObject {
    title: string;
    startTime: string;
    endTime?: string;
    tags: string[];
}

export class UserEvent {
    public title: string | undefined;
    public startTime: Date;
    public endTime?: Date;
    public tags: Set<string> = new Set();

    constructor(startTime: Date, title: string | undefined = undefined) {
        this.title = title;
        this.startTime = startTime;
    }

    public getDuration(): number | undefined {
        return this.endTime === undefined ? undefined : (this.endTime.getTime() - this.startTime.getTime());
    }
}
