import { UserEvent } from "./UserEvent"
import { UserEventStore } from "./UserEventStore";
import moment from 'moment';

class UserEventManager {
    userEvents: UserEvent[] = [];
    tags: Set<string> = new Set();

    constructor() {
        let store = new UserEventStore();
        this.userEvents = store.loadUserEvents();
        this.userEvents.forEach(e => {
            e.tags.forEach(t => {
                this.tags.add(t);
            })
        });
    }

    addEvent(event: UserEvent): void {
        this.userEvents.push(event);
        event.tags.forEach(t => {
            this.tags.add(t);
        });

        this.saveEvents();
    }

    getEvents(): UserEvent[] {
        return [...this.userEvents];
    }

    // getEventsByTags(tags: Set<string>): UserEvent[] {
    //     return this.userEvents.filter(x => {
    //         const intersection = Array.from(tags).filter(t => !x.tags.has(t));
    //         return intersection.length > 0;
    //     });
    // }

    getTags(): string[] {
        return [...this.tags];
    }

    clearEvents(): void {
        this.userEvents = [];

        this.saveEvents();
    }

    generateRandomEvents() {
        let now = new Date();
        let ago = 7;
        let events: UserEvent[] = [];
        let titles = ["A", "B", "C", "D", "E"];
        let tags = ["tagA", "tagB", "tagC"];
        for (let i = 0; i < 7; ++i) {
            for (let j = 0; j < 5; ++j) {
                let day = moment(now.toDateString()).add(-ago + i, "d");
                let hour = Math.floor(Math.random() * 24);
                let minute = Math.floor(Math.random() * 60);
                let startTime = day.add(hour, "h").add(minute, "m");
                let duration = Math.floor(Math.random() * 200);
                let tag = tags[Math.floor(Math.random() * 3)];
                let title = titles[Math.floor(Math.random() * 5)];

                let newEvent = new UserEvent(startTime.toDate());
                newEvent.endTime = startTime.add(duration, "m").toDate();
                newEvent.tags = new Set([tag]);
                newEvent.title = title;

                events.push(newEvent);
            }
        }
        this.userEvents = this.userEvents.concat(events);
        this.saveEvents()
    }

    private saveEvents() {
        let store = new UserEventStore();
        store.saveUserEvents(this.userEvents);
    }
}

export const instance = new UserEventManager();
