import './Bar.scss';
import * as UserEventManager from "../UserEventManager"
import React from 'react';
import moment from 'moment';
import { UserEvent } from '../UserEvent';
import {
    DiscreteColorLegend,
    HorizontalGridLines,
    makeWidthFlexible,
    VerticalBarSeries,
    VerticalGridLines,
    XAxis,
    XYPlot,
    YAxis
} from 'react-vis';
import { TimeUtil } from '../TimeUtil';

const FlexibleXYPlot = makeWidthFlexible(XYPlot);

function groupBy<T, K>(array: T[], keySelector: (arg0: T) => K) {
    return array.reduce((rv, x) => {
        let s = keySelector(x);
        if (rv.has(s)) {
            rv.get(s)!.push(x);
        } else {
            rv.set(s, [x]);
        }
        return rv;
    }, new Map<K, T[]>());
}

export class Bar extends React.Component<{ events?: UserEvent[] }> {
    state = {

    };

    render() {
        let events = this.props.events ?? UserEventManager.instance.getEvents();
        events = events.filter(e => e.title != undefined);
        var eventByType = groupBy(events, e => e.title);
        var days = Array.from(groupBy(events.map(e => TimeUtil.GetMonthDayHash(e.startTime)), h => h).keys()).sort();

        var timeByEventDay = Array.from(eventByType.keys()).map(type => {
            var events = eventByType.get(type);
            var eventsByDay = groupBy(events!, e => TimeUtil.GetMonthDayHash(e.startTime));
            return { type, times: days.map(day => ({ x: TimeUtil.GetMonthDayStringFromHash(day), y: eventsByDay.get(day)?.reduce((s, i) => s + moment.duration(i.getDuration()).asHours(), 0.0) ?? 0 })) };
        });

        const barWidth = 0.5;
        var colors = ['#12939A', "#5c94ed"];
        var eventTypes = Array.from(eventByType.keys());
        var eventColorMap = new Map<string, string>();
        for (let i = 0; i < eventTypes.length; ++i) {
            eventColorMap.set(eventTypes[i]!, "#" + Math.floor(Math.random() * 16777215).toString(16));
        }

        return (<div>

            <FlexibleXYPlot
                animation
                className="clustered-stacked-bar-chart-example"
                xType="ordinal"
                stackBy="y"
                height={400}
            >

                <DiscreteColorLegend
                    orientation="horizontal"
                    items={Array.from(eventColorMap.keys()).map(eventType => ({
                        title: eventType,
                        color: eventColorMap.get(eventType)
                    }))}
                />


                <VerticalGridLines />
                <HorizontalGridLines />
                <XAxis />
                <YAxis />
                {timeByEventDay.map(t =>
                    <VerticalBarSeries
                        barWidth={barWidth}
                        color={eventColorMap.get(t.type!)}
                        data={t.times}
                    />
                )}
            </FlexibleXYPlot>
        </div>);
    }


}